		</div>
		<ul class="recharge_list">
			<li v-for="(item,index) in rechargeLists.amountList" :key="item.id" @click="toConfirm(index)">
				<p class="recharge_list_left">
					<span class="text1">{{ item.amount+'元' }}</span>
					<span class="text2" v-if="item.amount == 50">暂不支持联通充值</span>
				</p>
				<p class="recharge_list_right">
					<span class="text1">{{ item.payAmount+'元' }}</span>
					<span class="text2"><i>{{ rechargeLists.discount+'折' }}</i></span>
				</p>
			</li>
		</ul>
