<script>
	
  export default {
    name:'payPhone',
    data() {
      return {
        rechargeLists:[],
		phoneNumber:''
      }
    },
    mounted() {
	  let _this = this;
	  var jsonObj = localStorage.getItem("obj");
	  _this.obj = JSON.parse(jsonObj);
	  _this.getPhoneRechargeConf();
    },
    methods:{
	  goBack(){
		this.$router.push({path:'/'})
	  },
	  goOrder(){
		  this.$router.push({path:'/payPhoneBill'})
	  },
	  getPhoneRechargeConf(){
		  let _this = this;
		  console.log(_this.obj);
		  var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
		  _this.$axios({
		    method: 'post',
		    url: '/getPhoneRechargeConf',
		    data:data,
		  }).then(function(res) {
		    if(res.data.code === 200){
				var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
				console.log(reData);
				_this.rechargeLists = reData;
		    }
		  });
	  },
	  toConfirm(i){
		  let _this = this;
		  var reg_tel = /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/;
		  if(reg_tel.test(_this.phoneNumber)){
			  _this.$router.push({path:'/payPhoneConfirm',query:{cashBack:_this.rechargeLists.cashBack,discount:_this.rechargeLists.discount,mobile:_this.phoneNumber,itemData:_this.rechargeLists.amountList[i]}})
		  }else{
			  _this.$toast('请填写正确的手机号！');
		  }
	  },
	  pay(i){
		  let _this = this;
		  var reg_tel = /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/;
		  if(reg_tel.test(_this.phoneNumber)){
			  
		  }else{
			_this.$toast('请填写正确的手机号！');
		  }
		  
		  
	  }
    }
  }
</script>

